      color: #fff;
    }
    input[type=submit]:active {
      border-top: 3px solid #009fe0;
      border-bottom: 0;
    }
  }


  .close-rwd {
  	display:block;
  	background:#fff;
  	height:55px;
  	width:160px;
  	position:absolute;
    margin: auto 0;
    bottom:-55px;
  	right:0px;
    padding: 3px;
    z-index: 6000;
    p {
      display: inline-block;
      color: $primary-color;
      text-transform: uppercase;
      padding-left: 7px;
    }
    i {
      color: $primary-color;
      font-size: 1.7rem;
      text-transform: uppercase;
      text-align: center;
      padding: 13px 9px;
      margin: 0;
      display: inline-block;
    }
  }
  #admin {
    display: none;
  }

}
